.mainHome {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
  }
  
  .mainHomeBox > h1 {
    font-weight: 500;
    font-size: 2.8rem;
  }
  
  .mainHomeBox > span {
    font-weight: 300;
    font-size: 1.4rem;
  }
  
  .mainHomeBoxPulse {
    border-right: solid 1px #fff;
    margin-left: 0px;
  }
  
  .mainHomeBoxPulse.active {
    border-right: solid 1px #000;
  }